import Vue from 'vue'
import Vuex from 'vuex'
import firebase from './firebase.js';

Vue.use(Vuex)

//import entries from './entries.js';

firebase.auth.onAuthStateChanged(user => {
	if (user) {
        store.commit('saveUser', user);
        store.dispatch('getUserProfile');
	}
	
	// Recibimos el stream de entradas (fotos)
	firebase.entriesCollection.orderBy('cuando', 'desc').onSnapshot(querySnapshot => {
        let entries = [];
        querySnapshot.forEach(doc => {
            let entry = doc.data();
            entry.id = doc.id;
            entries.push(entry);
        });
        store.commit('loadEntries', entries);
    });

});

const store = new Vuex.Store({
	state: {
		entries: null,
		user: null,
		userProfile: {},
		photoSubmission: {},
		submission: false,
		filters: [
			{ name: "normal" },
			{ name: "clarendon" },
			{ name: "gingham" },
			{ name: "moon" },
			{ name: "lark" },
			{ name: "reyes" },
			{ name: "juno" },
			{ name: "slumber" },
			{ name: "aden" },
			{ name: "perpetua" },
			{ name: "mayfair" },
			{ name: "rise" },
			{ name: "hudson" },
			{ name: "valencia" },
			{ name: "xpro2" },
			{ name: "willow" },
			{ name: "lofi" },
			{ name: "inkwell" },
			{ name: "nashville" }
		]
	},
	mutations: {
		loadEntries(state, val) {
			state.entries = val;
		},
		saveUser(state, val) {
			state.user = val;
		},
		saveUserProfile(state, val) {
			state.userProfile = val;
		},
		startSubmission(state, val) {
			state.submission = val;
		},
		assignImage(state, val) {
			state.photoSubmission.image = val;
		}
	},
	actions: {
		getUserProfile({ commit, state }) {
            firebase.usersCollection.doc(state.user.uid).get().then(res => {
                commit('saveUserProfile', res.data());
            }).catch(error => {
                console.error(error);
            })
		},
		cancelSubmission({commit}) {
			commit('startSubmission', false);
			commit('assignImage', {});
		},
		cleanUser({commit}) {
            commit('saveUser', null);
            commit('saveUserProfile', {});
        }
	}
});

export default store;

