<template>
  <section class="section">
    <div class="container">
      <ErrorMessages v-show="mensajeError" :mensaje="mensajeError" />
      <div class="card">
        <div class="card-content is-paddingless">
          <div
            class="imagen"
            :class="entry.filter"
            :style="{backgroundImage : `url(${entry.url})`}"
          ></div>
          <div class="datos">
            <p class="username is-size-7">Por {{ entry.username }} {{ entry.cuando | timeAgo }}</p>
            <p class="caption">{{ entry.caption}}</p>
          </div>
        </div>
        <footer class="card-footer">
          <div class="card-footer-item">
            <a @click.prevent="likePhoto" class="votar" href="#">
              <i class="fas fa-heart"></i>
            </a>
            <span class="votos">{{ entry.likes }} likes</span>
          </div>
          <div v-if="photoOwned" class="card-footer-item">
            <a @click.prevent="deletePhoto" href="#">Eliminar</a>
          </div>
        </footer>
      </div>
    </div>
  </section>
</template>

<script>
import firebase from "../firebase.js";
import moment from "moment";
import ErrorMessages from "./ErrorMessages.vue";
export default {
  name: "EntryItem",
  async created() {
    if (this.$store.state.user) {
      this.likeId = this.$store.state.user.uid + "_" + this.entry.id;
      try {
        let doc = await firebase.likesCollection.doc(this.likeId).get();
        this.liked = !doc.exists ? false : true;
      } catch (error) {
        console.error(error);
      }
    }
  },
  props: {
    entry: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      liked: true,
      mensajeError: ""
    };
  },
  methods: {
    async likePhoto() {
      if (this.liked === true) return;

      try {
        let saveLike = firebase.likesCollection.doc(this.likeId).set({
          photoId: this.entry.id,
          userId: this.$store.state.user.uid
        });

        let addLike = firebase.entriesCollection.doc(this.entry.id).update({
          likes: this.entry.likes + 1
        });

        await Promise.all([saveLike, addLike]);

        this.liked = true;
      } catch (error) {
        console.error(error.message);
      }
    },
    deletePhoto() {
      this.mensajeError = "";
      firebase.entriesCollection
        .doc(this.entry.id)
        .delete()
        .catch(error => {
          console.error(error);
          this.mensajeError = "Error eliminando imagen.";
        });
    }
  },
  computed: {
    photoOwned() {
      if (!this.$store.state.user) return false;
      
      return this.$store.state.user.uid === this.entry.userId;
    }
  },
  filters: {
    timeAgo(timestamp) {
      return moment(timestamp.toDate()).fromNow();
    }
  },
  components: {
    ErrorMessages
  }
};
</script>

